@import url('https://fonts.googleapis.com/css2?family=Baloo+2&display=swap');


header{
    display: flex; 
    justify-content: center;
}

header img {
    margin-top: 15px;;
    width: 45%;
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 25px;
  margin: 25px;
  background-color: #fff;
  box-shadow: 0 0 20px rgba(0,0,0,0.3);
  padding: 30px;
} 

.input {
  grid-column: 1 / -1;
  justify-self: center;

}

input{
    font-family: 'Baloo 2', sans-serif;
    font-size: 16px;
    padding: 5px 10px;
    border-radius: 5px;
    background-color: #FFF;
    color: #333;
    outline: none;
    box-shadow: none;
  }

  
input:focus {
    border-color: #ffbb00;
  }

.pokemon {
background: linear-gradient(45deg, #dcedc8, #b2dfdb, #ff00ff44);
  border: 5px solid #ffbb00;;
  border-radius: 25px;
  padding: 25px;
  justify-self: center;
  text-align: center;
  font-family: 'Baloo 2', sans-serif;
}

.pokemon img {
  background: #fffefd;
  padding: 35px;
  border-radius: 10px;
}

figcaption {
  font-family: 'Baloo 2', sans-serif;
  text-transform: capitalize;
  margin-top: 10px;
  font-size: 18px;
  font-weight: bold;
}

div.pokemon input {
  width: 50%; 
  text-align: center;
}

label {
  display: block;
  margin-left: 30px;
  text-align: left;
}

label > input {
  margin-left: 25px;
  margin-bottom: 5px;
  text-align: left;
}

label > input[type="number"] {
    margin-left: 28px;
}


label > input[type="checkbox"] {
    margin-left: 96px;
    width: 22px;
    height: 22px;
}

button {
  margin-top: 20px;
  background-color: #fffefd;
  font-family: 'Baloo 2', sans-serif;
  font-size: 18px;
  padding: 10px 55px;
  border: 2px solid #777777;
  border-radius: 10px;
  text-shadow: 1px 1px #999999;
  box-shadow: 2px 2px #CCCCCC;
  transition: all 0.2s ease-in-out;
}

.input input {
    font-size: 20px;
    padding: 15px 30px;
}

.input button {
    margin-left: 10px;
    font-size: 20px;
    padding: 15px 30px;
    border-radius: 5px;
}

button:hover {
  background-color: #CCCCCC;
  color: #333333;
  border: 2px solid #555555;
  box-shadow: 1px 1px #CCCCCC;
}

button:active {
  background-color: #AAAAAA;
  color: #FFFFFF;
  border: 2px solid #444444;
  box-shadow: none;
  transform: translate(0px, 0px);
}
